/* ShiftSummary table styles */
.shift-summary table {
  width: 100%;
  border-collapse: separate;
  border-spacing: 0;
  margin-bottom: 1rem;
  border-radius: 8px;
  overflow: hidden;
  border: 1px solid var(--border-color);
  text-align: left;
}

.shift-summary th,
.shift-summary td {
  padding: 0.75rem;
}

.shift-summary th {
  background-color: var(--primary-color);
  color: white;
  font-weight: bold;
}

.shift-summary tr:last-child td {
  border-bottom: none;
}

.shift-summary th:last-child,
.shift-summary td:last-child {
  border-right: none;
}

/* Mobile styles */
@media (max-width: 768px) {
  .App {
    padding: 1rem;
  }

  .container {
    flex-direction: column;
  }

  .schedule-section, .call-roster-section {
    width: 100%;
  }

  .header h1 {
    font-size: 2rem;
  }

  button {
    width: 100%;
    margin-right: 0;
    margin-bottom: 0.5rem;
  }

  /* Transposed table styles for mobile */
  .shift-summary table {
    border-collapse: separate;
    border-spacing: 0;
    border-radius: 8px;
    overflow: hidden;
    border: 1px solid var(--border-color);
  }

  .shift-summary table, 
  .shift-summary thead, 
  .shift-summary tbody, 
  .shift-summary th, 
  .shift-summary td, 
  .shift-summary tr {
    display: block;
  }

  .shift-summary thead tr {
    position: absolute;
    top: -9999px;
    left: -9999px;
  }

  .shift-summary tr {
    border-bottom: 1px solid var(--border-color);
  }

  .shift-summary tr:last-child {
    border-bottom: none;
  }

  .shift-summary td {
    border: none;
    position: relative;
    padding-left: 50%;
    text-align: right;
    min-height: 2.5rem;
  }

  .shift-summary td:before {
    content: attr(data-label);
    position: absolute;
    left: 0;
    width: 50%;
    white-space: nowrap;
    text-align: left;
    font-weight: bold;
    color: white;
    background-color: var(--primary-color);
    top: 0;
    bottom: 0;
    display: flex;
    align-items: center;
    padding-left: 0.75rem;
  }

  .shift-summary tr:first-child td:first-child:before {
    border-top-left-radius: 8px;
  }

  .shift-summary tr:last-child td:last-child:before {
    border-bottom-left-radius: 8px;
  }

  .shift-summary td:after {
    content: "";
    position: absolute;
    left: 40%;
    top: 0;
    bottom: 0;
    width: 1px;
  }
}