.export-buttons {
  display: flex;
  margin-top: 1.5rem;
}

.export-button {
  padding: 0.5rem 1rem;
  margin-right: 2em;
  background-color: var(--primary-color);
  color: white;
  border-radius: 4px;
  cursor: pointer;
  font-size: 1rem;
  transition: background-color 0.15s;
}

.export-button:hover:not(:disabled) {
  background-color: rgba(52, 152, 219, 0.8);
}

.export-button:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

@media (max-width: 768px) {
  .export-buttons {
    flex-direction: column;
  }
  
  .export-button {
    width: 100%;
  }
}
