.resident-form {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    margin-bottom: 1.5rem;
}
  
.resident-form label {
    font-weight: bold;
}

.resident-form button, .clear-button, .generate-button {
    padding: 0.5rem 1rem;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 1rem;
    transition: background-color 0.3s;
}

.resident-form button {
    background-color: var(--primary-color);
    color: white;
}

.resident-form button:hover {
    background-color: #2980b9;
}

.clear-button {
    background-color: #e74c3c;
    color: white;
}

.clear-button:hover {
    background-color: #c0392b;
}

.generate-button {
    background-color: var(--secondary-color);
color: white;
}

.generate-button:hover {
    background-color: #27ae60;
}

.action-buttons {
    display: flex;
    gap: 1rem;
    margin-top: 1rem;
}

.onespace {
    margin-left: 10px;
}

.days-off-info {
    font-weight: normal;
    margin: 2px 0;
}